import BilingualText from './BilingualText';
import ProjectData from './ProjectData';
import Link from './Link';

const WeJayData =
	new ProjectData(
		new BilingualText("weJay", ""),
		new BilingualText("Everyone's a DJ", "DJ"),
		[
			new BilingualText("Collaborative DJ web app that lets party-goers democratically choose what songs plays next", ""),
			new BilingualText("Users suggest songs from Soundcloud, upvote songs they like, and earn DJ points for popular choices", "SoundcloudDJ"),
			new BilingualText("Built using React, Redux, and Firebase", "React, Redux, Firebase")

		],
		[
			new Link(false, "https://github.com/christophermanahan/wejay", "zmdi zmdi-github zmdi-hc-2x"),
			new Link(true, "http://wejay.me", "public/logos/wejay.png", "img-wejay"),
			new Link(false, "https://www.youtube.com/watch?v=-XEOkNGz8m0", "zmdi zmdi-youtube-play zmdi-hc-2x"),
		]);

export default WeJayData;


