import BilingualText from './BilingualText';
import ProjectData from './ProjectData';
import Link from './Link';

const RatMapData =
	new ProjectData(
		new BilingualText("RatMap", ""),
		new BilingualText("Experience the real New York City", ""),
		[
			new BilingualText("Visualization for NYC Open Data's rat infestation inspections", ", "),
			new BilingualText("Users can filter by borough or location and calculate likelihood of infestation", ""),
			new BilingualText("Built using React, Redux, Socrata Web API, and Google Maps API", "React, Redux, SocrataAPI, API")

		],
		[
			new Link(false, "https://github.com/dillonius01/ratmap", "zmdi zmdi-github zmdi-hc-2x"),
			new Link(true, "http://ratmap.herokuapp.com", "/public/logos/icon-rat.png", "img-ratmap"),
			new Link(false, "https://www.youtube.com/watch?v=eCVuL15I0G4", "zmdi zmdi-youtube-play zmdi-hc-2x"),
		]);

export default RatMapData;


